.top-button-position {
   display: flex;
   align-items: center;
   width: 224px;
 }

.popover-selection-count {
  font-size: 14px;
  padding-left: 5px;
  flex-grow: 1;
  color: #24292e;
}

.card-view {
  display: flex;
  background: white;
  border-style: solid;
  border-width: 0.8px;
  border-radius: 4px;
  border-color: rgb(225, 225, 225);
  color: #707070;
  margin-bottom: 1em;
  width: 215px;
  height: 50px;

  align-items: center;
  justify-content: flex-start;

  margin-left: 0;
  padding-left: 0;
  left: 0;
}

.card-image-icon-preview {
  box-sizing: border-box;
  width: 32px;
  height: 32px;
  flex-shrink: 0;
  margin: 8px;
}

.card-body-title {
  width: 80px;
  display: inline-block;;
  font-size: 0.9em;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  color: #2f363d;
}

.card-body-subtitle {
  font-size: 12px;
  text-transform: uppercase;
  color: #586069;
}
