.object-quick-actions {
    position: absolute;
    top: 16px;
    right: 16px;
    left: unset;
    display: flex;
    justify-content: flex-end;
}

.object-quick-actions.list-view {
    top: 4px;
    right: 14px;
}

.spectrum-Card--small > .object-quick-actions {
    top: 10px;
    right: 10px;
    left: unset;
}

.quick-info-popover.spectrum-Popover--withTip {
    margin-top: 0;
    padding: 24px;
}

.quick-info-popover > .spectrum-Popover-tip {
    visibility: hidden;
}
