.breadcrumbs {
    flex-wrap: nowrap;
    display: flex;
    flex-grow: 1;
}

.breadcrumbs .spectrum-Breadcrumbs-item div {
    flex: 1;
    min-width: 0;
}

.breadcrumbs .spectrum-Breadcrumbs-item div a {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.breadcrumbs .spectrum-Breadcrumbs-item:first-of-type .spectrum-Breadcrumbs-itemLink {
    padding-left: 0;
}

.breadcrumbs.small > li:not(:first-of-type) {
    min-width: 0;
    overflow: hidden;
}
.breadcrumbs.small > li > svg.spectrum-Breadcrumbs-itemSeparator {
    flex-shrink: 0;
}

.breadcrumbs.small a.spectrum-Breadcrumbs-itemLink {
    overflow: hidden;
}

.breadcrumbs.small .overflow-container {
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
}

.search-results {
    display: flex;
    align-items: center;
    line-height: 32px;
    font-size: 16px;
}

.search-results > span {
    margin-left: 16px;
}
