.title {
  position: absolute;
  left: 50%;
  margin-left: -30px;
  padding-top: 40px;
  font-weight: bold;
  font-size: 18px;
}

.outer-box {
  width: 80%;
  height: 40px;
  position: absolute;
  margin-left: calc(10% + 10px);
  margin-right: calc(10% + 30px);
  margin-top: calc(10vh - 5px );
  padding-bottom: 30%;
  border-style: solid;
  border-color: #3385ff;
}

.inner-box {
  width: calc(15vw);
  height: 160px;
  margin-top: 80px;
  margin-left: 15px;
  margin-right: 15px;
  border-style: dashed;
  border-color: grey;
  display:inline-block;
}

.inner-items {
  display: flex;
  align-items: center;
}

.icon-styles-left {
  float: left;
  margin-left: 8%;
  margin-top: 60px;
}

.icon-styles-right {
  float: right;
  margin-right: 8%;
  margin-top: 60px;
}
