.filter-bar {
    display: flex;
}

.filters-bar {
    display: flex;
    align-items: center;
    width: 300px;
}

.filters-compact .filter-bar-search {
    width: 100%;
}

.filters-compact  .filter-bar-search > input {
    width: 100%;
}

.filter-bar > .filter-button {
    flex-grow: 0;
    flex-shrink: 0;
}

.filter-button + .filter-bar-search {
    margin-left: 8px;
}

.filter-button > .is-selected {
    background-color: rgb(225, 225, 225);
}

.filter-modal .spectrum-Dialog-content {
    height: 420px;
    width: 400px;
    overflow-y: scroll;
}


.filters-tags.formula-TagList {
    display: inline-block;
}

.filters-compact .filters-search {
    flex-grow: 1;
    padding-right: 4px;
}

.filters-taglist {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin-top: 15px;
}

.filters-taglist > span + .filters-tags {
    margin-left: 10px;
}

.filters-rail {
    width: 256px;
    height: 100%;
    overflow-y: scroll;
    border-right: rgb(225, 225, 225) solid 2px;
    flex-grow: 0;
    flex-shrink: 0;
    margin-right: 16px;
}
