
.object-picker-modal .layout-grid {
    width: 100%;
}

.object-picker-modal-container {
    height: 100%;
    display: flex;
    align-items: flex-start;
    flex-direction: row;
    overflow-y: hidden;
}

.object-picker-modal-main {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    height: 100%;
}

.object-picker-modal-main > div:not(:first-child) {
    margin-top: 15px;
}

.object-picker-top-bar {
    display: flex;
    align-items: center;
}

.object-picker-primary-bar {
     display: flex;
     align-items: center;
 }
