
.layout-table, .layout-list {
    box-sizing: border-box;
    height: 100%;
    width: 100%;
    min-height: 250px;
}

.layout-grid {
    box-sizing: border-box;
    height: 100%;
    width: 100%;
    min-height: 250px;
}

.layout-grid-container {
    height: 100%;
    display: flex;
    flex-direction: column;
}

.layout-grid-container > .layout-grid {
    height: unset;
    flex-grow: 1;
}

.layout-list .spectrum-Table-head {
    display: none; /* hide header row */
}

.layout-list .react-spectrum-TableView-row.spectrum-Table-row {
    background-color: white;
}

.layout-list-cell {
    display: flex;
    flex-direction: row;
    height: 100%;
    width: 100%;
    align-items: center;
    padding: 8px 0;
}

.layout-list-cell-preview {
    box-sizing: border-box;
    width: 32px;
    height: 32px;
    flex-shrink: 0;
}

.layout-list-cell-preview + .layout-list-cell-body {
    padding-left: 14px;
}

.layout-list-cell-body {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    overflow: hidden;
}

.layout-list-cell-title,
.layout-list-cell-subtitle {
    font-size: 14px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    padding-right: 8px;
}

.layout-list-cell-title {
    font-size: 14px;
    color: rgb(75, 75, 75);
}

.layout-list-cell-subtitle {
    font-size: 11px;
    letter-spacing: 0.06em;
    color: rgb(110, 110, 110);
}

.layout-list-cell-quickActions {
    position: absolute;
    right: 14px;
}

.layout-table-hover-cell {
    display: flex;
    align-items: center;
    width: 100%;
    height: 100%;
}

.layout-table-hover-cell-main {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.layout-masonry-folder-item {
    box-sizing: border-box;
    /*width: 208px;*/
    display: flex;
    align-items: center;
    cursor: pointer;
    border-radius: 3px;
    background-color: rgb(235, 235, 235);
}

.layout-masonry-folder-item:hover {
    background-color: rgb(225, 225, 225);
}

.layout-masonry-folder-item > .folder-icon {
    padding: 4px 8px;
}

.layout-masonry-item.spectrum-Card {
    min-width: unset
}

.overflow {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.overflow.overflow-start {
    direction: rtl;
    text-align: right;
}
