.filters-CheckboxGroup {
    display: block;
    margin-bottom: 5px;
}

.filters-CheckboxGroup-vertical span {
    display: block;
}

.filters-CheckboxGroup.filters-CheckboxGroup-main-column {
    display: flex;
    align-items: flex-start;
    justify-content: center;
}

.filters-CheckboxGroup-sub-column {
    flex-grow: 1;
    flex-shrink: 1;
}