.object-picker-split-view {
    width: 100%;
}

.spectrum-SplitView-splitter:not(.is-hovered) .spectrum-SplitView-gripper {
    display: none;
}

.object-picker-rail {
    display: flex;
    flex-direction: column;
}

.object-picker-rail .layout-grid {
    width: 100%;
}

.object-picker-rail > div {
    margin: 12px 16px 0 16px;
}

.object-picker-rail > nav {
    margin: 10px 16px 0 16px;
}

.object-picker-rail > .layout-switcher {
    margin: 0;
    top: -12px;
}

.object-picker-rail > .layout-switcher > .layout-list {
    margin-top: 6px;
}

.object-picker-rail .object-counter {
    margin-left: unset;
    line-height: unset;
}

.object-picker-rail .filters-taglist {
    margin-top: 12px;
}
